<template>
  <main class="SelectionContainer">

    <div v-if="!groupSelected" class="fisrtSelector">
      <div class="btFSelection" v-for="(item, index) in myGroups" :key="index" @click="mySelection(index, item.name)">
        <div class="btFS_Title">{{ item.name }}</div>
        <div class="btFS_Desc">{{ item.desc }}</div>
      </div>
    </div>

    <div v-else class="subSelector">

      <div class="subItensBtDiv">
        <button class="subItensBt" @click="voltar"> Voltar </button>
      </div>

      <div class="subItensTitle">
        <div class="subItensTitleTxt">{{ subItensName }}</div>
      </div>
      <div class="subItensContainer">
        <div class="subitensItens" v-for="(item, index) in groupSelected" :key="index" @click="fuiCricado(item)">
          {{ item.name }}
        </div>
      </div>

    </div>
  </main>
</template>

<script>

import { groups } from '@/utils/db.js';

import axios from 'axios'

import { io } from "socket.io-client";

// const socket = io("http://localhost:3000");
const socket = io("https://vizinhoapi.herokuapp.com/");

const http = axios.create()

export default {
  data() {
    return {
      subItensName: null,
      groupSelected: null,
      selectionPrefix: null,
      subSelection: null,
      ioclient: socket,
      myGroups: null,
      myGroups1: null,
      myGroups2: null,
      myGroups3: null,
    }
  },
  beforeMount() {

    this.verifySection()

  },
  mounted() {

    this.BASE_URL = "https://vizinhoapi.herokuapp.com/"
    // this.BASE_URL = "http://localhost:3000/"

    this.getGroups()
    this.verifyTriagem()

  },
  methods: {
    async enviaTriagem() {

      var lClient = localStorage.getItem('client')

      lClient = JSON.parse(lClient)

      lClient = lClient[0]

      // console.log({cl1:this.subItensName})
      // console.log({cl2:this.groupSelected})
      // console.log({cl3:this.selectionPrefix})
      // console.log({cl4:this.subSelection})

      try {
        const response = await http.post(this.BASE_URL + 'triagem/add', {
          client_id: lClient.idcliente,
          espec_prefix: this.selectionPrefix,
          atendimento: this.subItensName,
          espec: this.subSelection.name
        });

        this.ioclient.emit("ClientJoin", lClient.idcliente);

        if (response.data.response.status) {
          this.$router.push('/fila');
        }

      } catch (error) {

        // console.log({ error });

      }
    },
    verifySection() {

      let section = localStorage.getItem('secction_page')

      let lClient = localStorage.getItem('client')

      // console.log({ section })
      // console.log({ lClient })

      if (lClient === null) {
        localStorage.setItem('secction_page', 'home');
        localStorage.setItem('sPrefix', null);
        localStorage.setItem('sSelection', null);
        this.$router.push('/');
      }

      if (section === null) {

        localStorage.setItem('secction_page', 'home');
        localStorage.setItem('sPrefix', null);
        localStorage.setItem('sSelection', null);
        localStorage.setItem('client', null);
        this.$router.push('/');

      }

    },
    async verifyTriagem() {

      var lClient = localStorage.getItem('client')

      lClient = JSON.parse(lClient)

      lClient = lClient[0]

      // console.log({ triagem: lClient })

      // console.log({ id: lClient.idcliente })

      try {
        const response = await http.post(this.BASE_URL + 'triagem/consult', {
          client_id: lClient.idcliente
        });

        if (response.data.response.status) {
          this.$router.push('/fila');
        }

      } catch (error) {

        // console.log({ error });

      }

    },
    fuiCricado(Value) {
      this.subSelection = Value
      // console.log(this.subSelection)
      localStorage.setItem('sPrefix', this.selectionPrefix);
      localStorage.setItem('sSelection', JSON.stringify(this.subSelection));

      this.enviaTriagem()

      // this.$router.push('/fila')
    },
    voltar() {
      this.groupSelected = null
    },
    mySelection(Value, Nome) {

      // console.log({ botao: Value })

      this.subItensName = Nome

      switch (Value) {
        case 0:
          // console.log('Vou para espera como: Acolhimento')
          this.selectionPrefix = 'A'
          this.subSelection = { name: 'Pronto Atendimento' }
          localStorage.setItem('sPrefix', this.selectionPrefix);
          localStorage.setItem('sSelection', null);

          this.enviaTriagem()

          // this.$router.push('/fila')
          break;
        case 1:
          this.groupSelected = this.myGroups1
          this.selectionPrefix = 'S'
          break;
        case 2:
          this.groupSelected = this.myGroups2
          this.selectionPrefix = 'D'
          break;
        case 3:
          this.groupSelected = this.myGroups3
          this.selectionPrefix = 'B'
          break;
        default:
          // console.log('Operação seleciona não encontrada.')
          this.selectionPrefix = null
          break;
      }

      // console.log(this.selectionPrefix)

    },
    getGroups() {
      this.myGroups = groups[0]
      this.myGroups1 = groups[1]
      this.myGroups2 = groups[2]
      this.myGroups3 = groups[3]
    }
  },
}
</script>

